import React, {Component} from 'react';
import './App.css';
import SearchBar from './SearchBar.js';
import MenuBar from './MenuBar.js';
import PhotoCarousel from './PhotoCarousel.js';
import NavBar from './NavBar.js';
import 'materialize-css/dist/css/materialize.min.css'
import './index.css'
import './Modal.css';
import axios from 'axios';

class App extends Component {
  constructor(props) {
	super(props);

	this.state = {
	  idtoken: "",
	  email: "",
	  currAlbum: "default",
	  photos: []
	};
  }

  authCallBack = (data) => {
	this.setState({
	  idtoken: data.idtoken,
	  email: data.email
	});
  }

  setAlbumCallBack = (album) => {
	this.setState({
	  currAlbum: album
	});

	console.log("Album is now: " + album);

	axios.get('/albumPhotos', { params: { idtoken: this.state.idtoken, albumName: album} } ).then(resolve => {
	  this.setState({photos: resolve.data[0].album});	
	  //console.log(this.state.photos);
	}, err => {
	  console.log("Album doesn't exist!");
	});
  }

  render() {
    return (
      <div className="App" style={{background: '#FFFAFA'}}>
		  <NavBar getAuth={this.authCallBack} />
		  <br/> 
		  <h1>Stiny's Photos </h1>
		  <MenuBar />
		  <SearchBar setAlbum={this.setAlbumCallBack} />
		  <PhotoCarousel idtoken={this.state.idtoken} email={this.state.email} album={this.state.currAlbum} photos={this.state.photos}/>
		  <div className="rect">
		  </div>
      </div>
    );
  }
}

export default App;
