import React from 'react';
import axios from 'axios';
import M from "materialize-css";

const headersConfig = {
  "Content-Type": "multipart/form-data"
};

class AddPhoto extends React.Component {
  constructor(props) {
	super(props);
	this.myRef = React.createRef();
	this.createAlbumRef = React.createRef();
	this.photoRef = React.createRef();
	this.dateRef = React.createRef();
	this.captionRef = React.createRef();
	this.testRef = React.createRef();

	this.state = {
	  selectedAlbum: "",
	  message: ""
    };
  }

  componentDidUpdate(prevProps) {
	if (prevProps !== this.props.albumList) {
		let elems = document.querySelectorAll('select');
		M.FormSelect.init(elems);
	}
  }

  componentDidMount() {
	var elems = document.querySelectorAll('select');
    M.FormSelect.init(elems);
  }

  handleSubmit = () => {
	//const helperStatus = this.createAlbumRef.current;

	var bodyFormData = new FormData();
	bodyFormData.append('idtoken', this.props.token);
	bodyFormData.append('email', this.props.email);
	//bodyFormData.append('albumName', this.createAlbumRef.current.value);
	bodyFormData.append('albumName', this.state.selectedAlbum);
	bodyFormData.append('caption', this.captionRef.current.value);
	bodyFormData.append('date', this.dateRef.current.value);
	bodyFormData.append('image', this.photoRef.current.files[0]);

	/*
	for (var value of bodyFormData.values()) {
		console.log(value);
	}
	*/

	axios.post('/addPhoto', bodyFormData, { headers: headersConfig })
	  .then(response => {
		this.setState({message: "Added photo to " + this.state.selectedAlbum + " album!"});
	  }).catch(error => {
		if (error.response.status === 403) {
		  this.setState({message: "Could not add photo! Album does not exist."});
		} else if (error.response.status === 400) {
		  this.setState({message: "Could not add photo! Try relogging into your account."});
		}
	  });
	/*
	axios.post('/addPhoto', request)
		.then(function (response) {
		  //helperStatus.classList.toggle('valid');
		  console.log(response);
	    }).catch(function (error) {
		  //helperStatus.classList.toggle('invalid');
		  console.log(error);
        });
	*/
  }

  handleSelect = e => {
	this.setState({selectedAlbum: e.target.value});
  }

  render() {
	return (
	  <div
	    ref={this.myRef}
        id="modal3"
        className="modal"
		style={{"marginTop": "22%", "paddingTop": "20%", "backgroundColor": "rgb(255, 255, 255, 0.95"}}
      >
        <div className="modal-content" style={{}}>
          <a href="#!" className="modal-close waves-effect" style={{display: "inline-block"}}>
		    Close
          </a>
          <h4>Add Photos</h4>
		  <div className="row">
			<div className="input-field col s12">
			  <select defaultValue={this.state.selectedAlbum} onChange={this.handleSelect}>
			    <option value="" disabled>Choose Album</option>
				  {
				    this.props.albumList.map(item => (
					  <option value={item}>{item}</option>
					))
				  }
			  </select>
			</div>
			<div className="col s12">
			  <form action="#">
			<div className="file-field input-field">
			  <div className="btn">
				<span>File</span>
				<input type="file" ref={this.photoRef}/>
			  </div>
			  <div className="file-path-wrapper">
				<input className="file-path validate" type="text"/>
			  </div>
			</div>
		  </form>
			</div>
			<div className="input-field col s12">
			  <input ref={this.dateRef} id="date" type="text" placeholder="e.g. 12/12/2012" />
			  <label htmlFor="date">Date</label>
			</div>
			<div className="input-field col s12">
			  <input ref={this.captionRef} id="desc" type="text" placeholder="e.g. Walk in snow." />
			  <label htmlFor="desc">Caption</label>
			</div>
		  </div>
		  <p className="helper-text">{this.state.message}</p>
		  <a href="#!" ref={this.createAlbumRef} onClick={this.handleSubmit} className="waves-effect waves-light btn"><i className="material-icons left">cloud</i>Submit</a>
        </div>
      </div>
	);
  }
}

export default AddPhoto;
